import React from "react";
import styled from "styled-components";

const FormWrapper = styled.main`
  padding: 5px;
  border: 0.1rem solid var(--secondary);
  border-radius: 5px;
  transition: all 0.3s;
  max-width: 750px;
  margin: auto;
  position: relative;
`;

const Form = styled.form`
  padding: 37.5px;
  margin: auto;
  display: flex;
  flex-direction: column;
`;

const Input = styled.input`
  background: none;
  border: none;
  border-bottom: solid 1px var(--secondary);
  font-size: 14px;
  font-weight: 400;
  letter-spacing: 1px;
  margin: 0em 0 1.875em 0;
  padding: 0 0 0.875em 0;
  width: 100%;
  color: var(--secondary);

  &::placeholder {
    color: var(--secondary);
  }
`;

const Select = styled.select`
  outline: none;
  border: none;
  border-bottom: solid 1px var(--secondary);
  font-size: 14px;
  background: none;
  font-weight: 400;
  letter-spacing: 1px;
  margin: 0em 0 1.875em 0;
  padding: 0 0 0.875em 0;
  width: 100%;
  color: var(--secondary);
  option {
    color: var(--secondary);
    background: var(--primary);
    height: 20px;
  }
`;

const Option = styled.option``;

const TextArea = styled.textarea`
  line-height: 150%;
  height: 100px;
  resize: none;
  width: 100%;
  background: none;
  border: none;
  border-bottom: solid 1px var(--secondary);
  font-size: 14px;
  font-weight: 400;
  letter-spacing: 1px;
  margin: 0em 0 1.875em 0;
  padding: 0 0 0.875em 0;
  color: var(--secondary);
  &::placeholder {
    color: var(--secondary);
  }
`;

const SubmitButton = styled.button`
  background: none;
  border: solid 1px var(--secondary);
  cursor: pointer;
  display: inline-block;
  font-size: 0.875em;
  font-weight: bold;
  outline: none;
  padding: 20px 35px;
  color: var(--secondary);
`;

const Contact = ({}) => {
  const initialValues = {
    name: "",
    email: "",
    subject: "",
    message: "",
  };

  const googleForm = {
    nameId: "entry.785977059",
    emailId: "entry.2045089802",
    messageId: "entry.599553692",
    subjectId: "entry.1804229484",
    actionUrl:
      "https://docs.google.com/forms/u/0/d/e/1FAIpQLSctMAv-Gk_i9KzVR8-iOAeq97NidK1WPRosvAamdXnxZH0Yig/formResponse",
  };

  const [values, setValues] = React.useState(initialValues);

  const handleChange = (
    event: React.ChangeEvent<
      HTMLInputElement | HTMLSelectElement | HTMLTextAreaElement
    >
  ) => {
    setValues({
      ...values,
      [event.target.name]: event.target.value,
    });
  };

  const handleSubmit = (event: React.FormEvent<HTMLFormElement>) => {
    event.preventDefault();
    const formData = new FormData();
    formData.append(googleForm.nameId, values.name);
    formData.append(googleForm.emailId, values.email);
    formData.append(googleForm.subjectId, values.subject);
    formData.append(googleForm.messageId, values.message);
    fetch(googleForm.actionUrl, {
      method: "post",
      body: formData,
      mode: "no-cors",
      headers: {
        Accept: "application/json",
        "Access-Control-Allow-Origin": "*",
      },
    }).then(() => {
      setValues(initialValues);
    });
  };

  return (
    <FormWrapper>
      <Form onSubmit={handleSubmit}>
        <Input
          type="text"
          value={values.name}
          name="name"
          onChange={handleChange}
          placeholder="Name"
          required
        />
        <Input
          type="email"
          value={values.email}
          name="email"
          onChange={handleChange}
          placeholder="Email"
          required
        />
        <Select
          placeholder="Subject line"
          name="subject"
          value={values.subject}
          onChange={handleChange}
          required
        >
          <Option hidden>Subject line</Option>
          <Option>I&#39;d like to start a project</Option>
          <Option>I&#39;d like to ask a question</Option>
          <Option>I&#39;d like to make a proposal</Option>
        </Select>
        <TextArea
          value={values.message}
          name="message"
          onChange={handleChange}
          placeholder="Message"
          required
        />
        <SubmitButton type="submit" value="Submit">
          Contact
        </SubmitButton>
      </Form>
    </FormWrapper>
  );
};

export default Contact;
